body {
  box-sizing: border-box;
  --main-text-color: #000;
  --semi-text-color: #00acdc;
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
}

header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__list {
  text-transform: uppercase;
  list-style: none;
  display: flex;
}

.nav__item {
  position: relative;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--semi-text-color);
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.nav__link {
  color: var(--main-text-color);
  cursor: pointer;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--semi-text-color);
}
